<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->

        <el-form-item label="工序名称">
          <el-input
            v-model="listQuery.production_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="工站">
          <el-select v-model="listQuery.work_id" placeholder="请选择">
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' search'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>工序列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增工序弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>
          <el-row :gutter="20">
            <el-col :span="12">
              <!-- <el-form-item label="工序编码" prop="production_id">
                <el-input v-model="dialogForm.production_id"></el-input>
              </el-form-item> -->

              <el-form-item label="工站" prop="work_id">
                <el-select
                  v-model="dialogForm.work_id"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option
                    v-for="(item, index) in WorkCodeNameList"
                    :key="index + ' search'"
                    :label="item.work_name"
                    :value="item.work_id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <!-- <el-form-item label="工序名称" prop="production_name">
                <el-input v-model="dialogForm.production_name"></el-input>
              </el-form-item> -->

              <!-- <el-form-item label="上级工站" prop="leader_work_id">
                <el-select
                  v-model="dialogForm.leader_work_id"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option
                    v-for="(item, index) in WorkCodeNameList"
                    :key="index + ' dialogForm'"
                    :label="item.work_name"
                    :value="item.work_id"
                  ></el-option>
                </el-select>
              </el-form-item> -->
            </el-col>
          </el-row>

          <el-form-item label="备注" prop="description">
            <el-input
              type="textarea"
              v-model="dialogForm.description"
            ></el-input>
          </el-form-item>
          <p class="dialog-title">工站允许加工物料明细</p>
          <el-button type="primary" size="small" plain @click="AddListRow()"
            >添加物料</el-button
          >
          <el-table
            :data="dialogForm.response_datas"
            style="width: 100%"
            class="dialogTable"
            height="240"
          >
            <el-table-column fixed="left" label="物料名称" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_code'"
                  :rules="[
                    {
                      required: true,
                      message: '物料不能为空',
                    },
                  ]"
                >
                  <el-select
                    v-model="scope.row.matter_code"
                    placeholder="请选择"
                    style="width: 100%"
                    @change="dialogMatterCodeChange(scope.$index, scope.row)"
                  >
                    <el-option
                      v-for="(item, index) in BOMResponseDatas"
                      :key="index + ' search'"
                      :label="item.matter_name"
                      :value="item.bom_matter_code"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="规格型号" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.rule'"
                >
                  <span>{{ scope.row.rule }}</span>
                  <!-- <el-input v-model="scope.row.rule" readonly></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="类型" align="center" width="100">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_category'"
                >
                  <span>{{ scope.row.matter_category }}</span>
                  <!-- <el-input v-model="scope.row.matter_category"></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="最大数量" align="center" width="100">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_usage'"
                >
                  <span>{{ scope.row.matter_usage }}</span>
                  <!-- <el-input v-model.number="scope.row.matter_usage"></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="已安装数量" align="center" width="100">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.installed_number'"
                >
                  <span>{{ scope.row.installed_number }}</span>
                  <!-- <el-input v-model.number="scope.row.matter_usage"></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="安装数量" align="center" width="100">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.install_number'"
                  :rules="[
                    {
                      required: true,
                      message: '数量不能为空',
                      trigger: 'blur',
                    },
                    {
                      type: 'number',
                      message: '数量必须为数值',
                      trigger: 'blur',
                    },
                    {
                      type: 'number',
                      min: 0,
                      max: scope.row.matter_usage - scope.row.installed_other_number,
                      message:
                        '不能超过-' +
                        (scope.row.matter_usage - scope.row.installed_other_number),

                      trigger: 'blur',
                    },
                  ]"
                >
                  <!-- max: scope.row.matter_usage,
                      message: '不能超过' + (scope.row.matter_usage), -->
                 
                    <!-- @input="install_number_blur(scope.$index, scope.row)" -->
                  <el-input
                    v-model.number="scope.row.install_number"
                  ></el-input>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="操作" width="100" align="center">
              <template slot-scope="scope">
                <el-form-item>
                  <el-popover placement="right" trigger="click">
                    <el-table :data="gridData">
                      <el-table-column
                        property="work_name"
                        label="工站"
                        align="center"
                        width="150"
                      ></el-table-column>
                      <el-table-column
                        property="install_number"
                        label="安装数量"
                        align="center"
                        width="100"
                      ></el-table-column>
                    </el-table>
                    <el-button
                      slot="reference"
                      @click="handleDetail(scope.$index, scope.row)"
                      type="text"
                      size="small"
                      >查看
                    </el-button>
                  </el-popover>

                  <el-button
                    style="margin-left: 5px"
                    @click="handleDelete(scope.$index, scope.row)"
                    type="text"
                    size="small"
                    >删除
                  </el-button>
                  <!-- <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(scope.$index, scope.row)" >删除
                    </el-button > -->
                </el-form-item>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "ProcessList",
  components: {
    ELTable,
  },

  computed: {
    ...mapState([
      "WorkCodeNameList",
      "ProcessCodeNameList",
      "NoPagePersonMatterData",
    ]),
  },
  data() {
    return {
      gridData: [],
      // 筛选
      listQuery: {
        production_name: "",
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        production_name: "",
        work_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增工序弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        production_code: "",
        production_id: "",
        production_name: "",
        work_id: "",
        leader_work_id: "",
        description: "",
        response_datas: [
          {
            process_matter_deal_code: "",
            matter_code: "",
            rule: "--",
            matter_category: "--",
            matter_usage: "--",
            install_number: "",
            installed_number: 0,
          },
        ],
      },
      dialogFormBase: {
        production_code: "",
        production_id: "",
        production_name: "",
        work_id: "",
        leader_work_id: "",
        description: "",
        response_datas: [
          {
            process_matter_deal_code: "",
            matter_code: "",
            rule: "--",
            matter_category: "--",
            matter_usage: "--",
            install_number: "",
            installed_number: 0,
          },
        ],
      },
      dialogFormRules: {
        // production_id: [
        //   { required: true, message: "请输入工序编码", trigger: "blur" },
        // ],
        // production_name: [
        //   { required: true, message: "请输入工序名称", trigger: "blur" },
        // ],
        work_id: [{ required: true, message: "请选择工站", trigger: "change" }],
      },
      // 表格列
      tableCol: [
        // {
        //   label: "工序编码",
        //   field: "production_id",
        //   align: "center",
        // },
        // {
        //   label: "工序名称",
        //   field: "production_name",
        //   align: "center",
        // },
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },

        // {
        //   label: "上级工站",
        //   field: "leader_work_id",
        //   align: "center",
        // },
        {
          label: "备注",
          field: "description",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      BOMResponseDatas: [],
      tableData: [],
    };
  },
  methods: {
    ...mapActions([
      "getLogisticWorkCodeName",
      "getLogisticProcessNameCode",
      "getLogisticNoPagePersonMatter",
    ]),

    // 产品结构 查询
    getLogisticBOMProductListInit() {
      let params = {
        product_name: "",
        rule: "",
        product_status: "",
        page: 1,
        page_size: 10,
        total: 0,
      };
      baseManagement.getLogisticBOMProductList(params).then((res) => {
        console.log("产品结构 查询", res);
        if (res.code == 0) {
          this.BOMResponseDatas = res.data.data[0].response_datas;
          console.log("1232132132132133", this.BOMResponseDatas);
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticProcessDeal(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticProcessDeal(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticProcessDeal(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticProcessDeal(this.listQuery);
    },
    // 工序列表 查询
    async getLogisticProcessDeal(params) {
      baseManagement.getLogisticProcessDeal(params).then((res) => {
        console.log("工序列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.work_id == CodeNameListItem.work_id) {
                tableDataItem.work_name = CodeNameListItem.work_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 工序列表 新增
    postLogisticProcessDeal(params) {
      baseManagement.postLogisticProcessDeal(params).then((res) => {
        console.log("工序列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticProcessDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 工序列表 新增
    addBtn() {
      this.dialogTitle = "新增工序";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    AddListRow() {
      this.dialogForm.response_datas.push({
        process_matter_deal_code: "",
        matter_code: "",
        rule: "",
        matter_category: "",
        matter_usage: "",
        install_number: "",
      });
    },

    dialogMatterCodeChange(index, rowData) {
      let install_number_sum = 0;
      baseManagement
        .getLogisticNoPageProcessMatterDeal({
          matter_code: rowData.matter_code,
          // work_id: rowData.work_id,
        })
        .then((res) => {
          // console.log("各工站的物料安装数量", res);
          if (res.code == 0) {
            let tableData = res.data;
            tableData.forEach((tableDataItem) => {
              install_number_sum += tableDataItem.install_number;
            });
          }
          this.dialogForm.response_datas[index].installed_number =
            install_number_sum +
            this.dialogForm.response_datas[index].install_number;
        });

      this.BOMResponseDatas.forEach((item, index) => {
        if (rowData.matter_code == item.bom_matter_code) {
          rowData.rule = item.rule;
          rowData.matter_category = item.matter_category;
          rowData.matter_usage = item.matter_usage;
        }
      });
    },
    // 工序列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改工序";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      rowData.response_datas.forEach((rowDataItem, rowDataIndex) => {
        console.log(rowDataItem);

        let install_number_sum = 0;
        let installed_other_number_sum = 0;
        baseManagement
          .getLogisticNoPageProcessMatterDeal({
            matter_code: rowDataItem.matter_code,
            // work_id: rowDataItem.work_id,
          })
          .then((res) => {
            console.log("各工站的物料安装数量", res);
            if (res.code == 0) {
              let tableData = res.data;
              tableData.forEach((tableDataItem) => {
               
                install_number_sum += tableDataItem.install_number;
                if(tableDataItem.work_id!=rowData.work_id){
                  installed_other_number_sum+=tableDataItem.install_number
                }
              });
            }
            rowDataItem.installed_number = install_number_sum;
            rowDataItem.installed_other_number = installed_other_number_sum;
          });

        if (!rowDataItem.process_matter_deal_code) {
          rowData.response_datas.splice(rowDataIndex, 1);
        }

        this.BOMResponseDatas.forEach((BOMResponseDatasItem) => {
          if (rowDataItem.matter_code == BOMResponseDatasItem.bom_matter_code) {
            rowDataItem.rule = BOMResponseDatasItem.rule;
            rowDataItem.matter_category = BOMResponseDatasItem.matter_category;
            rowDataItem.matter_usage = BOMResponseDatasItem.matter_usage;
          }
        });
      });
      console.log(
        "rowData.response_datas rowData.response_datas -=-=- ",
        rowData.response_datas
      );
      this.dialogForm = {
        production_code: rowData.production_code,
        production_id: rowData.production_id,
        production_name: rowData.production_name,
        work_id: rowData.work_id,
        code_count: rowData.code_count,
        leader_work_id: rowData.leader_work_id,
        description: rowData.description,
        response_datas: rowData.response_datas,
      };
    },

    // 工序列表 修改
    getLogisticPutProcessDeal(params) {
      baseManagement.getLogisticPutProcessDeal(params).then((res) => {
        console.log("工序列表 修改", res);
        if (res.code == 0) {
          this.getLogisticProcessDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      console.log(this.dialogTitle);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增工序") {
            console.log("新增工序", this.dialogForm);
            this.postLogisticProcessDeal(this.dialogForm);
          } else if (this.dialogTitle == "修改工序") {
            console.log("修改工序", this.dialogForm);

            let params = JSON.parse(JSON.stringify(this.dialogForm));

            params.response_datas.forEach((item) => {
              console.log(item);
            });
            params.response_datas = JSON.stringify(params.response_datas);
            this.getLogisticPutProcessDeal(params);
          }
        }
      });
    },

    // 工序列表 删除
    getLogisticDeleteProcessDeal(params) {
      baseManagement.getLogisticDeleteProcessDeal(params).then((res) => {
        console.log("工序列表 删除", res);
        if (res.code == 0) {
          this.getLogisticProcessDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // async install_number_blur(index, rowData, value) {
    //   console.log(1111111, rowData);
    //   // console.log(2222, this.dialogForm.response_datas[index]);

    //   let install_number_sum = 0;
    //   await baseManagement
    //     .getLogisticNoPageProcessMatterDeal({
    //       matter_code: rowData.matter_code,
    //       work_id: rowData.work_id,
    //     })
    //     .then((res) => {
    //       // console.log("各工站的物料安装数量", res);
    //       if (res.code == 0) {
    //         let tableData = res.data;
    //         tableData.forEach((tableDataItem) => {
    //           install_number_sum += tableDataItem.install_number;
    //         });
    //       }
    //       console.log(222, install_number_sum);
    //       // rowDataItem.installed_number = install_number_sum + rowDataItem.install_number;
    //       this.dialogForm.response_datas[index].installed_number =install_number_sum;

    //     });

    //   // this.dialogForm.response_datas[index].installed_number =install_number_sum;
    //   console.log(333, rowData.install_number);
    // },

    //  点击删除当前行 查看
    handleDetail(index, rowData) {
      baseManagement
        .getLogisticNoPageProcessMatterDeal({
          matter_code: rowData.matter_code,
          work_id: rowData.work_id,
        })
        .then((res) => {
          // console.log("各工站的物料安装数量", res);
          if (res.code == 0) {
            let tableData = res.data;
            tableData.forEach((tableDataItem) => {
              this.WorkCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.work_id == CodeNameListItem.work_id) {
                  tableDataItem.work_name = CodeNameListItem.work_name;
                }
              });
            });
            this.gridData = tableData;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    //  点击删除当前行 删除
    handleDelete(index, rowData) {
      console.log("============rowData ", rowData);
      if (rowData.process_matter_deal_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            production_code: rowData.production_code,
            response_datas: JSON.stringify([
              { process_matter_deal_code: rowData.process_matter_deal_code },
            ]),
          };
          console.log("paramsparamsparams====", params);
          this.getLogisticDeleteProcessDeal(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },

    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteProcessDeal({
          production_code: rowData.production_code,
          response_datas: JSON.stringify([]),
        });
      });
    },
  },
  mounted() {
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 工序内码和名称接口： 查询
    this.getLogisticProcessNameCode();
    // 不分页的物料详情表接口
    this.getLogisticNoPagePersonMatter();
    // 产品结构 查询
    this.getLogisticBOMProductListInit();
    // 工序列表 查询
    this.getLogisticProcessDeal(this.listQuery);
  },
};
</script>

